
import { defineComponent, PropType } from "vue";

export default defineComponent({
  name: "CountrySelect",
  props: {
    countries: Array as PropType<any[]>,
  },
  data() {
    return {
      selected: 0,
    };
  },
  methods: {
    onChange() {
      const country = this.countries?.find((c) => c.ID === this.selected);
      this.$emit("get-country", country);
    },
  },
});
